      oPetCare.style.backgroundPositionY = -parallaxValue + 'px';
    }
  },
  mounted() {
    AOS.init();

    window.addEventListener('scroll', this.handleParallaxEffect);
  },
  
  unmounted() {
    window.removeEventListener('scroll', this.handleParallaxEffect);
